<template>
  <div class="carousel">
    <div class="carousel-inner" :style="{ transform: `translateX(-${currentIndex * 100}%)` }">
      <div class="carousel-item" v-for="(image, index) in images" :key="index">
        <img :src="image" :alt="'Image ' + index" />
      </div>
    </div>
    <div class="dot-indication">
      <i class="bi bi-circle-fill" v-for="(image, index) in images" :key="image" @click="changeManual(index)"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CarrouselComponent',
  data() {
    return {
      images: ['bannerSuporte.jpg', 'bannerVoip.jpg', 'bannerMultiatendimento.jpg', 'bannerDesenvolvimento.jpg'],
      currentIndex: 0,
      intervalId: null
    };
  },
  mounted() {
    this.alternateImages()
  },
  methods: {
    changeCarrouselImage() {
      this.currentIndex = (this.currentIndex + this.images.length + 1) % this.images.length;
    },
    alternateImages(){
      this.intervalId = setInterval(this.changeCarrouselImage, 5000)
    },
    changeManual(index){
      this.currentIndex = index
      clearInterval(this.intervalId)
      this.intervalId = setInterval(this.changeCarrouselImage, 5000)
    }
  },
}
</script>

<style scoped>
.carousel {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.carousel-inner {
  display: flex;
  transition: transform 0.5s ease;
  height: 50vh;
